.text {
  margin-top: 5.625rem;
}
.text__para {
  margin-top: 1.5625rem;
}
@media (min-width: 71.875em) {
  .text {
    max-width: 31.25rem;
    margin: 18.125rem auto auto 10.3125rem;
  }
}

.panel {
  margin: 4.375rem 0;
}
@media (min-width: 71.875em) {
  .panel {
    margin-left: 1.25rem;
    max-width: 33.75rem;
  }
}
.panel__try {
  background-color: #6055a5;
  border-radius: 0.625rem;
  padding: 1.25rem 4.375rem;
  box-shadow: 0 0.5rem rgba(0, 0, 0, 0.25);
}
.panel__form {
  margin-top: 1.5625rem;
  background-color: white;
  border-radius: 0.625rem;
  box-shadow: 0 0.5rem rgba(0, 0, 0, 0.25);
}

.form {
  padding: 1.5625rem;
}
@media (min-width: 71.875em) {
  .form {
    padding: 2.5rem;
  }
}
.form__input {
  width: 100%;
  margin-bottom: 1rem;
}
@media (min-width: 71.875em) {
  .form__input {
    margin-bottom: 1.25rem;
  }
}
.form__input__text {
  width: 100%;
  padding: 1.25rem 1.875rem;
  border: 0.0625rem solid #b9b6d3;
  border-radius: 0.3125rem;
}
.form__input__text:focus {
  border-color: #6055a5;
  outline: none;
}
.form__input__btn {
  background-color: #38cc8c;
  width: 100%;
  padding: 1.25rem 1.875rem;
  border-radius: 0.3125rem;
  border: none;
  box-shadow: inset 0 -0.3125rem rgba(0, 0, 0, 0.25);
}
.form__input__btn:focus {
  outline: none;
}
.form__input__btn:hover {
  filter: opacity(50%);
}
.form__terms {
  padding: 0 1.5rem;
}

.form__input__error {
  position: relative;
  display: none;
}
.form__input__error div {
  margin-top: 0.3125rem;
}
.form__input__error img {
  position: absolute;
  transform: translate(-50%, -50%);
  top: -2.125rem;
  right: 1.5625rem;
}

.invalid {
  border: 0.125rem solid #ff7a7a;
  color: #ff7a7a;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
  background: url(/images/bg-intro-desktop.png), #ff7a7a;
}
@media (max-width: 39.9375em) {
  body {
    width: 87%;
    margin: auto;
  }
}
@media (min-width: 71.875em) {
  body {
    display: grid;
    align-items: center;
    grid-template-columns: 1fr 1fr;
  }
}

h1 {
  padding: 0;
  margin: 0;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1rem;
  color: white;
}

.text {
  text-align: center;
}
@media (min-width: 71.875em) {
  .text {
    text-align: left;
  }
}
.text__header {
  font-size: 1.75rem;
  line-height: 2.25rem;
  font-weight: 700;
}
@media (min-width: 71.875em) {
  .text__header {
    font-size: 3.375rem;
    line-height: 3.5rem;
  }
}
.text__para {
  font-weight: 500;
}

.panel {
  text-align: center;
}
.panel__try {
  font-size: 0.875rem;
  font-weight: 400;
}
.panel__try span {
  font-weight: 700;
}

.form__input {
  color: #3e3c49;
}
.form__input__text {
  font-size: 0.75rem;
  font-weight: 600;
}
.form__input__text:focus {
  color: #3e3c49;
}
.form__input__error div {
  text-align: right;
  color: #ff7a7a;
  font-size: 0.625rem;
  font-style: oblique;
  font-weight: 600;
}
.form__input__btn {
  font-family: "Poppins", sans-serif;
  font-size: 1rem;
  font-weight: 700;
  text-transform: uppercase;
  color: white;
}
.form ::placeholder {
  font-family: "Poppins", sans-serif;
  font-size: 0.875rem;
  font-weight: 600;
}
.form__terms {
  font-size: 0.625rem;
  font-weight: 400;
  color: #b9b6d3;
}
.form__terms span {
  color: #ff7a7a;
  font-weight: 700;
}